.form-element{display:grid;cursor:default;grid-template:auto/minmax(270px, 1fr) minmax(0, 150px) 25px;margin-bottom:10px}.form-element:hover{background:rgba(128,128,128,.2);border-radius:10px}.form-element__checkbox-label{grid-column:1/2}.form-element__custom-checkbox{cursor:pointer;height:29px;width:29px;border:1px solid #000;border-radius:10px;margin-top:7px;margin-bottom:7px;text-align:center;line-height:29px;font-size:29px}.form-element__required-title{font-size:12px;color:#999;display:flex;flex-direction:row;cursor:pointer;justify-self:center;align-self:center}.form-element__required-checkbox{display:none}.form-element__required-checkbox:checked+.form-element__required-custom-checkbox::after{content:"✓"}.form-element__required-custom-checkbox{cursor:pointer;height:15px;width:15px;border:1px solid #999;border-radius:5px;margin:0;margin-top:1px;margin-left:10px;text-align:center;line-height:15px;font-size:15px}.form-element__remove-btn{cursor:pointer;justify-self:end;height:20px;grid-column:3/4}.form-element__remove-btn::after{content:"❌"}.form-element__dragable-img{height:20px;margin-left:5px;justify-self:end;align-self:center;grid-column:3/4;cursor:grab}.form-element__input{background:none;height:51px;margin-top:7px;margin-bottom:7px;box-sizing:border-box;padding:0 26px;border-radius:10px;border:1px solid #000;grid-column:1/3}.form-element__input:hover{border:1px solid #000}.form-element__input:disabled{background:none;border-radius:10px}.form-element__input_type_label{padding:0;height:24px;border:none;font-size:15px;line-height:24px;grid-column:1/2;margin:0}.form-element__input_type_label:disabled:hover{border:none}.form-element__input_type_checkbox{display:none}.form-element__input_type_checkbox:checked+.form-element__custom-checkbox::after{content:"✓"}.form-element__input_type_checkbox:disabled+.form-element__custom-checkbox{background:none;cursor:default}.form-element__input_type_textarea{padding-top:12px;padding-bottom:12px;resize:none}